"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import logo from "@/images/onlylogo.png";

function getAverageColorFromImage(img: HTMLImageElement, sample = 6) {
  const canvas = document.createElement("canvas");
  const ctx = canvas.getContext("2d");
  if (!ctx) return null;

  const w = (canvas.width = 80);
  const h = (canvas.height = 80);
  // draw image scaled to canvas
  ctx.drawImage(img, 0, 0, w, h);

  try {
    const data = ctx.getImageData(0, 0, w, h).data;
    let r = 0,
      g = 0,
      b = 0,
      count = 0;

    // sample pixels for speed
    for (let y = 0; y < h; y += sample) {
      for (let x = 0; x < w; x += sample) {
        const idx = (y * w + x) * 4;
        const alpha = data[idx + 3];
        if (alpha === 0) continue; // skip transparent
        r += data[idx];
        g += data[idx + 1];
        b += data[idx + 2];
        count++;
      }
    }

    if (count === 0) return null;
    r = Math.round(r / count);
    g = Math.round(g / count);
    b = Math.round(b / count);
    return { r, g, b };
  } catch (e) {
    return null;
  }
}

function rgbToCss({ r, g, b }: { r: number; g: number; b: number }) {
  return `rgb(${r}, ${g}, ${b})`;
}

function luminance(r: number, g: number, b: number) {
  // relative luminance
  const a = [r, g, b].map((v) => {
    v /= 255;
    return v <= 0.03928 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, 2.4);
  });
  return 0.2126 * a[0] + 0.7152 * a[1] + 0.0722 * a[2];
}

export default function CTAWithLogoColor() {
  const [bg, setBg] = useState<string>("#0f172a"); // fallback slate-900
  const [textColor, setTextColor] = useState<string>("white");

  useEffect(() => {
    // create an image and sample its average color
    const img = new Image();
    img.crossOrigin = "anonymous";
    img.src = (logo as any).src || logo;

    const onLoad = () => {
      const avg = getAverageColorFromImage(img, 6);
      if (!avg) return;
      const css = rgbToCss(avg);
      setBg(css);
      const lum = luminance(avg.r, avg.g, avg.b);
      // choose readable text color
      setTextColor(lum > 0.5 ? "#0f172a" : "white");
    };

    img.addEventListener("load", onLoad);
    img.addEventListener("error", () => {
      // keep fallback
    });

    return () => {
      img.removeEventListener("load", onLoad);
    };
  }, []);

  // subtle gradient overlay for better contrast
  const backgroundStyle = `linear-gradient(rgba(0,0,0,0.06), rgba(0,0,0,0.06)), ${bg}`;

  return (
    <section
      className="rounded-3xl p-12 text-center"
      style={{ background: backgroundStyle, color: textColor }}
    >
      <div className="flex items-center justify-center gap-4 mb-4">
        <div className="w-12 h-12 relative rounded-md overflow-hidden shadow-sm bg-white/30">
          <Image src={logo} alt="logo" fill sizes="48px" style={{ objectFit: "contain" }} />
        </div>
      </div>

      <h2 className="text-3xl font-bold">
        Let’s Build Something <span className="text-yellow-400">Great</span>
      </h2>

      <p className="mt-4 max-w-2xl mx-auto" style={{ color: textColor === "white" ? "rgba(255,255,255,0.9)" : "rgba(15,23,42,0.9)" }}>
        Whether you’re a startup, business, or institution —
        TantraDOT is ready to transform your ideas into powerful digital solutions.
      </p>

      <Link
        href="/contact"
        className="inline-block mt-8 px-8 py-3 rounded-md font-semibold shadow-md"
        style={{ background: textColor === "white" ? "#FBBF24" : "#FBBF24", color: "#0f172a" }}
      >
        Contact Us
      </Link>
    </section>
  );
}
